import Vue from 'vue'
import App from './App'
import router from './router' 
import 'assets/css/index.less' 
import filter from 'assets/js/filter' 
import msg from 'assets/js/msg' 
import routerIntercept from 'assets/js/routerIntercept' 
import store from 'store' 
import ajax from 'assets/js/ajax' 
import dbClick from 'assets/js/dbClick' 
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import FastClick from 'assets/js/fastclick' 
/*引入移动端手势库*/
import directives from './directives/touch'
directives(Vue);


Vue.directive('transfer-dom', TransferDom)
Vue.use(msg)
Vue.use(routerIntercept)
Vue.use(ajax)
Vue.use(dbClick)
FastClick.attach(document.body)

Vue.config.productionTip = false


/* eslint-disable no-new */
new Vue({
el: '#app',
router,
filter,
store,
render: h => h(App)
})



